<template>
  <div class="wc">
    <div class="right">
      <ul class="flwz">目录</ul>
      <ul @click="gps_right(this)" class="lb" v-html="right_data"></ul>
    </div>
    <div class="left">
      <div class="article">
        <ul class="dh">{{getOne}} / {{getTwo}}</ul>
        <div class="head">
          <h1>{{title}}</h1>
          <span>发布日期:</span>
          <span class="rq">{{time}}</span>
          <span>&nbsp;&nbsp;&nbsp;&nbsp;分类：{{getOne}}</span>
        </div>
        <div class="body" v-html="html"></div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import { mapActions } from "vuex";
import { marked } from "../marked/marked";

export default {
  name: "HelloWorld",
  data() {
    return {
      html: "",
      title: "",
      time: "",
      right: {}, //右侧导航
      getOne: "",
      getTwo: "",
      dj: 1, //右侧导航是几级目录
      right_data: ""
    };
  },
  methods: {
    addID(data) {
      for (var i = 0; i < data.length; i++) {
        data[i].id = data[i].innerHTML;
      }
    },
    gps_right(a) {
      // console.log(a, this);
    },
    //结构化右侧目录
    jgh(data) {
      // console.log(data);
      var stru = "";
      for (var i in data) {
        stru += `<jg${this.dj}>${i}</jg${this.dj}>`;
        if (typeof data[i] != "object") {
          this.dj -= data[i];
        } else {
          this.dj++;
          stru += this.jgh(data[i]);
        }
      }
      return stru;
    },
    xr() {
      var data = () =>
        import(`../data/${this.json[this.getOne][this.getTwo]["content"]}`);
      data().then(a => {
        var data = a.func(marked);
        this.title = data.title;
        this.html = data.comtent;
        this.time = data.time;
        this.right_data = this.jgh(data.structure);
      });
    }
  },
  computed: {},
  created() {
    this.getOne = this.$route.query.getOne;
    this.getTwo = this.$route.query.getTwo;
    this.xr();
    setTimeout(() => {
      var that = this;
      document.getElementsByClassName("lb")[0].onclick = function(e) {
        that.addID(document.getElementsByTagName("bt4"));
        that.addID(document.getElementsByTagName("bt2"));
        that.addID(document.getElementsByTagName("bt3"));
        var top = document.getElementById(e.target.innerHTML).offsetTop - 60;
        var id1 = setInterval(() => {
          var Y1 = window.scrollY + 80;
          window.screen.height;
          document.body.scrollHeight;

          if (Y1 >= top) {
            Y1 = top;
            clearInterval(id1);
          } else {
          }
          window.scrollTo({ top: Y1 });
          if (Y1 + window.screen.height >= document.body.scrollHeight) {
            window.scrollTo({
              top: document.body.scrollHeight - window.screen.height+1000
            });
            clearInterval(id1);
          }
        }, 3);
        // window.scrollTo({
        //   top: document.getElementById(e.target.innerHTML).offsetTop - 60
        // });
        // console.log(
        //   document.getElementById(e.target.innerHTML).offsetTop,
        //   window.scrollY
        // );
      };
    }, 1);
  }
};
</script>

<style scoped lang='less'>
@media screen and (max-width: 720px) {
  .wc {
    display: block;
  }
  .left,
  .right {
    width: 100%;
  }
  .right {
    height: auto;
    padding: 10px 0;
  }
}
@media screen and (min-width: 720px) {
  .wc {
    display: flex;
  }
  .left {
    width: 70%;
  }
  .right {
    width: 27%;
    height: 450px;
  }
}
.wc {
  // display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;

  .left {
    // width: 70%;
    padding-bottom: 20px;
    box-sizing: border-box;
    .article {
      .head {
        margin: 0 0 20px 0;
        span {
          color: #686868;
          line-height: 2;
        }
        h1 {
          line-height: 2;
        }
      }
      .dh {
        color: #8d9095;
        margin-bottom: 30px;
      }
    }
  }
  .right {
    // width: 27%;

    box-sizing: border-box;
    padding: 10px;

    // border: 1px solid red;
    background: #f5f5f5;
    border-radius: 10px;
    .flwz {
      font-size: 20px;
      font-weight: bolder;
      margin: 0 0 10px 0;
    }
    .lb {
      box-sizing: border-box;
      height: 90%;
      overflow-y: scroll;
    }
  }
}
</style>



